<template>
  <div class="engineeringList">
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" >
      <template v-slot:nav-right>
          <div class="rightBtn" @click="onClickRight">{{rightName}}</div>
      </template>
    </navigation-bar>
    <div class="engineeringListContent">
      <ul class="engineering-list">
        <li
          v-for="(item,engineerIndex) in engineeringList"
          :key="engineerIndex"
          @click="gotoEngineerDetail(item)"
        >
          <div class="backCon" :style="{backgroundColor:item.color}"></div>

          <div class="engineerCon">
            <span class="engineerInfo">{{item.TITLE}}</span>
          </div>
          <div>
            <span class="engineerInfo">{{item.CREATED_TIME}}</span>
          </div>
          <img :src="rightArrowImg" />
        </li>
      </ul>
      <!--工程进展评论-->
      <div class="moment-list" v-if="commentList.length>0">
        <div class="moment-title">
          <span class="fontSize14">评论({{commentCount}})</span>
          <span class="more fontSize14" @click="gotoMore()">
            查看更多
            <img :src="rightArrowImg" />
          </span>
        </div>
        <div class="engineer-list" v-for="(item,commentIndex) in commentList" :key="commentIndex">
          <!--评论人头像-->
          <div class="head-img">
            <img
              :src="headImg"
              v-if="item.PIC_URL == undefined || item.PIC_URL == ''"
              class="head-picture"
            />
            <img
              :src="item.PIC_URL"
              v-if="item.PIC_URL != undefined && item.PIC_URL != ''"
              class="head-picture"
            />
          </div>

          <!--评论人昵称-->
          <div class="comment-con">
            <div class="fontSize12">
              <span class="font-style">{{item.USER_NICK_NAME}}</span>
              <span
                class="office-holder"
                v-if="item.BE_OWNER_CODE != undefined && item.TYPE_FLAG == 1"
              >地产</span>
              <span
                class="fontSize14"
                v-if="item.BE_OWNER_CODE != undefined && item.BE_OWNER_CODE != ''"
              >
                回复
                <span class="font-style">{{item.BE_OWNER_NICK_NAME}}</span>
              </span>
              <span class="fontSize14">:{{item.CONTENT}}</span>
            </div>
            <div class="comment-time">
              <span class="fontSize14">{{item.CREATED_TIME}}</span>
              <span
                class="fontSize14"
                style="padding-left: 20px"
                v-if="item.USER_CODE == ownerCode"
                @click="replayOperat(item,commentIndex)"
              >删除</span>
              <span class="reply" @click="replyComment(item)">
                <img :src="replyImg" />
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--底部评论框-->
    <div
      keyboardshoweng
      v-show="showFlag"
      class="bar item-input-inset"
      id="commentKeyboard"
      ref="commentKeyboard"
    >
      <label class="item-input-wrapper">
        <van-icon name="edit" style="margin-top:9px;" />
        <input
          type="text"
          ref="commnetInputEng"
          id="commnetInputEng"
          :placeholder="add.name"
          class="content"
          v-model="add.content"
          maxlength="200"
          ng-change="inputValidation()"
        />
      </label>
      <button class="button button-positive" @click="sendComment()">评论</button>
    </div>
  </div>
</template>

<script>
import {
  getEngineerService,
  getCommentsService,
  addCommentService,
  deleteCommentService
} from "../../../services/propertyService";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import Vue from "vue";
import { Dialog } from "vant";
Vue.use(Dialog);
export default {
  name: "engineeringList",
  components:{
      NavigationBar
  },
  data() {
    return {
      engineeringList: [],
      commentList: [],
      commentCount: 0,
      showFlag: false,
      add: { content: "", name: "说点什么" },
      replyInfo: {
        BE_OWNER_CODE: "",
        BE_OWNER_NICK_NAME: "",
        BE_OWNER_NAME: "",
        BE_PHONE: "",
        BE_PIC_URL: ""
      },
      headerItemWidth:{
          width:'84px'
      },
      title:'工程进展',
      rightName:'评论',
      headImg: require("../../../assets/images/home/property/engineering/head.png"),
      replyImg: require("../../../assets/images/home/property/engineering/reply.png"),
      rightArrowImg: require("../../../assets/images/home/property/engineering/right_black.png"),
      ownerCode: this.$userInfo.OWNER_CODE,
      marTop: { marginTop: this.$stbHeight + 46 + "px" }
    };
  },
  mounted() {
    cordova.plugins.Keyboard.hideKeyboardAccessoryBar(false);

    window.addEventListener("native.keyboardshow", e => {
      if (device.platform == "Android")
        this.$refs.commentKeyboard.style.bottom = e.keyboardHeight + 20 + "px";
      else this.$refs.commentKeyboard.style.bottom = e.keyboardHeight + "px";

      this.showFlag = true;
    });
    window.addEventListener("native.keyboardhide", e => {
      this.$refs.commentKeyboard.style.bottom = "0px";
      this.showFlag = false;
    });

    this.getEngineer();
    this.getComments();
  },
  methods: {
    getEngineer() {
      let reqData = {
        USER_CODE: this.$userInfo.OWNER_CODE,
        AREA_CODE: this.$userInfo.AREA_CODE /*业主所在小区*/
      };
      getEngineerService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result == "") {
          } else {
            this.engineeringList = result;
            for (var i = 0; i < this.engineeringList.length; i++) {
              this.engineeringList[i].color =
                "rgb(" +
                Math.floor(Math.random() * 256) +
                "," +
                Math.floor(Math.random() * 256) +
                "," +
                Math.floor(Math.random() * 256) +
                ")";
            }
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    getComments() {
      let reqData = {
        AREA_CODE: this.$userInfo.AREA_CODE /*业主所在小区*/
      };
      getCommentsService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result == "") {
            this.commentCount = result.length;
          } else {
            this.commentCount = result.length;
            this.commentList = result;
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    ///随机生成颜色
    getRandomColor() {
      return (
        "#" +
        (function(color) {
          return (color += "0123456789abcdef"[
            Math.floor(Math.random() * 16)
          ]) && color.length == 6
            ? color
            : arguments.callee(color);
        })("")
      );
    },
    onClickLeft() {
      cordova.plugins.Keyboard.hideKeyboardAccessoryBar(true);
      this.$router.back();
    },
    /* 点击评论弹出 */
    onClickRight() {
      this.showFlag = true;
      this.add.content = "";
      this.replyInfo = {
        BE_OWNER_CODE: "",
        BE_OWNER_NICK_NAME: "",
        BE_OWNER_NAME: "",
        BE_PHONE: "",
        BE_PIC_URL: ""
      };
      this.add.name = "说点什么";
      setTimeout(() => {
        this.$refs.commnetInputEng.focus();
      }, 100);
    },
    /* 点击回复弹出 */
    replyComment(reply) {
      console.log("点击回复");
      this.showFlag = true;
      this.add.content = "";
      this.add.name = "回复 " + reply.USER_NICK_NAME + "：";
      this.replyInfo.BE_OWNER_CODE = reply.USER_CODE;
      this.replyInfo.BE_OWNER_NICK_NAME = reply.USER_NICK_NAME;
      this.replyInfo.BE_OWNER_NAME = reply.USER_NAME;
      this.replyInfo.BE_PHONE = reply.USER_PHONE;
      this.replyInfo.BE_PIC_URL = reply.PIC_URL;
    },
    sendComment() {
      var reqData = {
        AREA_CODE: this.$userInfo.AREA_CODE,
        AREA_NAME: this.$userInfo.AREA_NAME,
        CITY_CODE: this.$userInfo.CITY_CODE,
        DEPT_CODE: this.$userInfo.DEPT_CODE,
        CONTENT: this.add.content,
        USER_CODE: this.$userInfo.OWNER_CODE,
        USER_NAME: this.$userInfo.OWNER_NAME,
        USER_NICK_NAME: this.$userInfo.OWNER_NICK_NAME,
        USER_PHONE: this.$userInfo.OWNER_PHONE,
        PIC_URL: this.$userInfo.PIC_URL,
        TYPE_FLAG: "0",
        BE_OWNER_CODE: this.replyInfo.BE_OWNER_CODE,
        BE_OWNER_NICK_NAME: this.replyInfo.BE_OWNER_NICK_NAME,
        BE_OWNER_NAME: this.replyInfo.BE_OWNER_NAME,
        BE_PHONE: this.replyInfo.BE_PHONE,
        BE_PIC_URL: this.replyInfo.BE_PIC_URL,
        UUID_PARENT: ""
      };
      if ("" === this.add.content || null === this.add.content) {
        this.$toast("说点什么，再评论吧", "center");
        return;
      }
      addCommentService({ data: JSON.stringify(reqData) })
        .then(answer => {
          if (answer.success) {
            this.getComments();
            this.showFlag = false;
            //清空评论内容 ZHSQYYPT-5028
            this.add.content = "";
          } else {
            this.$toast(answer.msg);
          }
        })
        .catch(err => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 回复删除按钮点击 */
    replayOperat(replay, index) {
      // this.showFlag = false;
      // window.cordova.plugins.Keyboard.close();
      Dialog.confirm({
        title: "提示",
        message: "确认删除评论？"
      }).then(() => {
        this.delMoments(replay, index);
      });
    },
    /* 删除评论 */
    delMoments(item, index) {
      var data = {
        UUID: item.UUID,
        USER_CODE: this.$userInfo.OWNER_CODE
      };
      deleteCommentService({ data: JSON.stringify(data) }).then(answer => {
        if (answer.success) {
          this.$toast(answer.msg);
          this.getComments();
        } else {
          this.$toast(answer.msg);
        }
      });
    },
    /* 跳转工程进展详情 */
    gotoEngineerDetail(item) {
      this.$router.push({
        name: "engineeringDetail",
        params: {
          projectCode: item.PROJECT_CODE
        }
      });
    },
    /* 跳转更多评论页 */
    gotoMore() {
      this.$router.push({
        name: "engineeringMore"
      });
    }
  }
};
</script>

<style scoped>
.engineeringList {
  height: 100%;
  background-color: #f2f2f2;
  display: flex;
  flex-direction: column;
}
.engineeringList .rightBtn{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 32px;
    padding-right: 10px;
    width: 100%;
    text-align: right;
}
.engineeringListContent {
  flex: 1;
  overflow-y: scroll;
}

.engineeringList .nav-right {
  position: absolute;
  /*color: #fff;*/
  right: 5px;
}
.engineeringList .main-header .left-img {
  width: 70px;
  height: 70px;
  vertical-align: text-bottom;
}
.engineeringList .main-header .right-con {
  display: inline-block;
}

.engineeringList .engineering-list {
  padding: 5px 10px;
}
.engineeringList .engineering-list .backCon {
  position: absolute;
  top: 0;
  display: inline-block;
  border-radius: 5px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  width: 100%;
  height: 5px;
}
.engineeringList .engineering-list .engineerCon {
  padding-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 90%;
}
.engineeringList .engineering-list li {
  position: relative;
  background: #fff;
  line-height: 15px;
  margin-bottom: 8px;
  border-radius: 5px;
  padding-bottom: 5px;
}
.engineeringList .engineering-list li span.engineerInfo {
  height: 25px;
  line-height: 25px;
  padding-left: 10px;
  font-size: 14px;
}
.engineeringList .engineering-list li > img {
  position: absolute;
  right: 8px;
  top: 18px;
  width: 20px;
  height: 20px;
}

.engineeringList .engineer-list {
  position: relative;
  background: #fff;
  padding: 5px 0 0 10px;
  overflow: hidden;
}
.engineeringList .engineer-list .head-img {
  float: left;
  width: 50px;
  margin-top: 5px;
}
.engineeringList .engineer-list .comment-con {
  min-height: 50px;
  margin-left: 50px;
  padding: 5px 5px 10px 0;
  border-bottom: 1px solid #f8f8f8;
}
.engineeringList .engineer-list .comment-time {
  position: relative;
  /* margin-top: 10px; */
  color: #a19e97;
}
.engineeringList .engineer-list .reply {
  display: inline-block;
  position: absolute;
  right: 10px;
  top: 3px;
  width: 20px;
  text-align: right;
}
.engineeringList .engineer-list .reply img {
  width: 15px;
}
.comment-con .office-holder {
  background: #ff3300;
  color: #fff;
  padding: 0 5px;
  border-radius: 8px;
  font-size: 14px;
}
.engineeringList .head-picture {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.engineeringList .font-style {
  color: #4c618e;
  font-size: 14px;
}
.engineeringList .fontSize14 {
  font-size: 14px;
}
.engineeringList .moment-list .moment-title {
  background-color: #fff;
  line-height: 40px;
  padding: 0 10px;
  border-bottom: 1px solid #f8f8f8;
}
.engineeringList .moment-list .moment-title .more {
  float: right;
}
.engineeringList .moment-list .moment-title .more > img {
  width: 15px;
  height: 15px;
  vertical-align: text-bottom;
}
/* 评论输入 */
.item-input-inset {
  width: 96%;
  padding: 10px;
  overflow: hidden;
  display: flex;
}
.bar.item-input-inset .item-input-wrapper {
  display: flex;
  border-radius: 4px;
  padding-right: 8px;
  padding-left: 8px;
  background: #eee;
  font-size: 14px;
  flex: 1 0;
}
.bar.item-input-inset .item-input-wrapper input {
  padding-left: 8px;
  width: 94%;
  height: 28px;
  background: transparent;
  border: 0;
}
.item-input-wrapper ~ .button {
  margin-left: 10.66667px;
}
.button.button-positive {
  border-color: transparent;
  background-color: #fb5768;
  color: #fff;
}
.bar .button,
.bar button {
  z-index: 1;
  padding: 0 8px;
  min-width: initial;
  min-height: 31px;
  font-weight: 400;
  font-size: 13px;
  line-height: 32px;
}
.button {
  border-color: transparent;
  background-color: #f8f8f8;
  color: #444;
  position: relative;
  display: inline-block;
  margin: 0;
  padding: 0 12px;
  min-width: 52px;
  min-height: 47px;
  border-width: 1px;
  border-style: solid;
  border-radius: 4px;
  vertical-align: top;
  text-align: center;
  text-overflow: ellipsis;
  font-size: 16px;
  line-height: 42px;
  cursor: pointer;
}
#commentKeyboard {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: white;
}

@media (min-height: 680px) {
  #commentKeyboard {
    position: fixed;
    bottom: 0;
  }
}
</style>
